import React, {Component} from 'react';
import classes from './Management.css';
import axios from '../../axios-orders';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom';
import Button from '../../components/UI/Button/Button';

class Management extends Component {
    state = {
        newPrices: this.props.ingredientPrices
    }


    pricesForm = () => Object.keys(this.state.newPrices).map(ingredient => {
            return (
                <div key={ingredient}>
                    <label>{ingredient}: </label>
                    <input type="number" id={ingredient} value={this.state.newPrices[ingredient]} onChange={(event) => this.inputChangedhandler(ingredient, event.target.value)}/>
                </div>
            );
        }
    );
    
    inputChangedhandler = (ingredient, value) =>{
        this.setState({
            newPrices:{
                ...this.state.newPrices,
                [ingredient]: +value
            }
        });
    }    

    updatePriceHandler = () => {
        if (this.state.newPrices === this.props.ingredientPrices) {
            alert('No changes to prices were made. No price update took place.');
        }
        else {
            let confirmation = window.confirm('You are about to update prices. Confirm?');
            if (confirmation) {
                axios.put('/data/prices.json', this.state.newPrices)
                    .then(response => {
                        alert('Prices updated!');
                        this.setState({newPrices: null});
                    })
                    .catch(error => {
                        console.log('An error has occured!' + error);
                    });
            }
        }
    }

    render () {
        const pricesDisplay = (
            <div>
                {!this.state.newPrices ? <Redirect to="/" /> :  this.pricesForm()}
                <Button btnType="Success" clicked={this.updatePriceHandler}>UPDATE</Button>
            </div>);
        

        return (
            <div className={classes.Management}>
                <h3>Define ingredient prices</h3>
                {pricesDisplay}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        ingredientPrices: state.burgerReducer.prices
    };
};


export default connect(mapStateToProps)(Management);


